@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaBT-Book.woff2") format("woff2"), url("../font/FuturaBT-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaCyrillic-Demi.woff2") format("woff2"), url("../font/FuturaCyrillic-Demi.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Futura";
  src: url("../font/FuturaBT-Medium.woff2") format("woff2"), url("../font/FuturaBT-Medium.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
:root {
  --pc: #C92B21;
  --sc: #F3C342;
  --body-color: #000;
  --gray-bg: #E5E7E9;
  --footer-bg: #272727;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #999999;
  --h1toh6: #4E4E4D;
  --line-light: #D9D9D9;
  --btn-hover: red;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-eb: 800;
  --font-black: 900;
  --font-eblack: 950;
}

body,
html {
  font-family: "Futura";
  line-height: 1.5;
  font-size: 24px;
  color: var(--body-color);
}

* {
  font-family: "Futura";
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-sb);
  font-size: min(3.6805555556vw, 6.625vh);
}

.h2,
h2 {
  font-weight: var(--font-sb);
  font-size: min(2.5vw, 4.5vh);
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: min(2.0833333333vw, 3.75vh);
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: min(1.6666666667vw, 3vh);
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: min(1.4583333333vw, 2.625vh);
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: min(1.1111111111vw, 2vh);
}

p {
  color: var(--p-color);
  font-weight: var(--font-r);
}

a {
  color: var(--pc);
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: auto;
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

@media (max-width: 1920px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: min(115.5555555556vw, 208vh);
  }
}
.pc {
  color: var(--pc);
}

.sc {
  color: var(--sc);
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes lineroad {
  to {
    stroke-dashoffset: 0;
  }
}
@media (max-width: 1400px) {
  html,
body {
    font-size: 16px;
  }
}
